<template>
	<div :style="css">{{message}}</div>
</template>
<script>
export default{
	props:["y","message","author"],
	computed:{
		css(){
			return{
				"--y":this.y,
				"--x":this.author=="ME"?"70%":"10%",
			}
		}
		
		
	}
}
</script>
<style scoped>
div{
	position:absolute;
	left:var(--x);
	top:var(--y);
}
</style>